.info-product {
  font-size: 14px;

  // .info-product__add-favorite
  &__add-favorite {
  }

  // .info-product__description
  &__description {
    margin-top: 20px;
    line-height: 1.3;
  }

  // .info-product__params
  &__params {
    margin-top: 15px;
  }

  // .info-product__links
  &__links {
    display: flex;
    margin-top: 24px;
    line-height: 1.1;
  }

  // .info-product__guarantee
  &__guarantee {
    margin-right: 35px;
    color: rgb(128, 128, 128);
    display: flex;

    &::before {
      content: '';
      margin-right: 5px;
      flex: 0 0 21px;
      height: 24px;
      background-image: url(../../img/i-guarantee.jpg);
      display: inline-block;
    }
  }

  // .info-product__call-manager
  &__call-manager {
    margin-right: 35px;
    color: rgb(128, 128, 128);
    display: flex;

    &::before {
      content: '';
      margin-right: 5px;
      flex: 0 0 21px;
      height: 24px;
      background-image: url(../../img/i-call-manager.jpg);
      display: inline-block;
    }
  }

  // .info-product__prices
  &__prices {
    margin-top: 33px;
  }

  // .info-product__actions
  &__actions {
    margin-top: 15px;
    color: rgb(128, 128, 128);
  }

  // .info-product__add-to-basket
  &__add-to-basket {
  }
}

.add-to-basket {
  // .add-to-basket--product-detail
  &--product-detail {
    background-color: rgb(0, 86, 147);
    text-transform: uppercase;
    padding: 15px 46px;
    font-weight: 700;
    font-size: 14px;
    border-radius: 30px;
    color: rgb(255, 255, 255);
    transition: all 0.3s;
    border: 2px solid rgb(0, 86, 147);

    &:hover,
    &:active,
    &:focus {
      background-color: #fff;
      color: #005693;
      outline: none;
    }
  }
  // .add-to-basket--added
  &--added {
    pointer-events: none;
    background-color: rgb(237, 98, 31);
    border-color: rgb(237, 98, 31);
  }
}

.add-favorite-btn {
  text-transform: uppercase;
  background-color: transparent;
  display: flex;
  align-items: center;
  margin-right: 18px;

  // .add-to-basket--product-detail--active
  &--active,
  &:active,
  &:focus,
  &:hover {
    color: rgb(237, 98, 31);
    outline: none;
  }

  // .add-to-basket--product-detail--hidden
  &--hidden {
    display: none;
  }

  &::before {
    content: '';
    margin-right: 5px;
    width: 13px;
    height: 19px;
    background-image: url(../../img/i-favorite.jpg);
    display: inline-block;
  }
}

