  // .prices-product--old
  &--old {
  }

  // .prices-product__item
  &__item {
  }
}

.price-product {
}

.images-product {
  max-width: calc(100% - 90px);
  border: 1px solid #e7e7e7;

  // .images-product__container
  &__container {
    margin-left: auto;
    margin-right: auto;
    position: relative;
    overflow: hidden;
    list-style: none;
    padding: 0;
    z-index: 1;
  }

  // .images-product__wrapper

  &__wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 1;
    display: flex;
    transition-property: transform;
    box-sizing: content-box;
  }

  // .images-product__slide
  &__slide {
    flex-shrink: 0;
    width: 100%;
    height: 100%;
    position: relative;
    transition-property: transform;
    text-align: center;

    img {
      width: 100%;
    }
  }
}
